*{
    margin:0;
    padding:0;
    text-decoration: none;
    --white: #ffffff;
    --green1: #00ff00;
    --green2: #18c305;
    --green3: #169000;
    --green4: #0d5503;
    --black: #000000 ;
    --greenLowO:#0d55030e;
    font-family: "Ubuntu", sans-serif;
    font-optical-sizing: auto;
   
    font-style: normal;
}

li {
    list-style-type:none;
}
.ubuntu-regular {
    font-family: "Ubuntu", sans-serif;
    font-weight: 400;
    font-style: normal;
  }
  
  .ubuntu-bold {
    font-family: "Ubuntu", sans-serif;
    font-weight: 700;
    font-style: normal;
  }
  
  .ubuntu-regular-italic {
    font-family: "Ubuntu", sans-serif;
    font-weight: 400;
    font-style: italic;
  }
  
  .ubuntu-bold-italic {
    font-family: "Ubuntu", sans-serif;
    font-weight: 700;
    font-style: italic;
  }




body {
    background-color: #d1d1d1;
}

.container{
    display:flex;
}
main{
    width: 97%;
  
    margin-left: auto;
    margin-right: auto;

    margin-top: 80px;
}
.sidebar{
    z-index: 100;
    background: linear-gradient(-45deg, #00ff0021, #18c30523, #16900079, #ffffff2f);
    border-bottom: 2px solid var(--green4);
	background-size: 400% 400%;
	animation: gradient 25s ease infinite;
    color: #102b00;
    height: 75px;
    width: 100%;
    max-width: 1350px;
    transition: all 0.5s;
    position: fixed;
    top: 0;
    left: 0;
    right: 0;
    margin-left: auto;
    margin-right: auto;
    display: grid;
    grid-template-columns: repeat(auto-fit, minmax(1rem, 1fr)) ;
    gap: 0.5rem;
    align-items: center;
    justify-content: center;
    align-content: center;
    align-self: center;
    backdrop-filter: blur(4px);
    border-bottom-left-radius: 0.5rem;
    border-bottom-right-radius: 0.5rem;

}
.top_section{
    display: flex;
    display: flex;
    flex-direction: row;
    align-items:center;
    padding:20px 15px;
    align-items: center;
    max-width: 70px;
}
.logo{
    height: 100%;
    max-width: 50px;
    transition: all 1s;
    width: 50px;
}
.bars{
    display: flex;
    font-size: 25px;
    margin-left: 50px;
}

.logo:hover {
    transform: rotateY(360deg);
}


.link{
    display: flex;
    color: #102b00;
    padding: 10px ;
    max-width: 120px;
    transition: all 0.3s;
    align-self: center;
    align-items: center;
    justify-content: center;
    border-radius: 0.5rem;
}
.link:hover{
    background:var(--green4);
    color: #ffffff;
    transition: all 0.3s;
    border-radius: 0.5rem;
    font-weight: 700;
}
.active{
    background: lightskyblue;
    color: #000;
    align-self: center;
    align-items: center;
    justify-content: center;
}
.icon, .link_text{
    font-size:1rem;
}


section {
    width: 100%;
    height: 75vh;
    margin-top: 1em;
    margin-bottom: 0.5em;
    background-color: var(--greenLowO);
    border-radius: 0.5rem;

}

.section2 {
    width: 100%;
    height: 75vh;
    margin-top: 1em;
    margin-bottom: 0.5em;
    background-color: #ffffff93;
    border-radius: 0.5rem;
}

.NavigationButtons {
    display: flex;
    width: 100%;
    margin-left: auto;
    margin-right: auto;
    justify-content: center;
    align-items: center;
    align-content: center;
   
}

.bbbfallinganimation {
    width: 100vw;
    height: 60vh;
    position: absolute;
}

.HomeSection {
    display: grid;
    width: 98%;
    align-items: center;
    align-self: center;
    padding: 1rem;
}
.mainTitle {
    font-size: 5rem;
}

.MainTitleContainer {
    padding: 1rem;
    padding-right: 3rem;
    backdrop-filter: blur(4px);
    background-color: #5bce191f;
    border-radius: 1rem;
    border-bottom: 2px solid var(--green4);
    height: 50vh;
    justify-content: center;
    align-items: center;
    align-self: center;
    display: flex;
    background: linear-gradient(-45deg, #00ff0059, #18c30569, #16900079, #ffffff8c);
	background-size: 400% 400%;
	animation: gradient 15s ease infinite;
}

@keyframes gradient {
	0% {
		background-position: 0% 50%;
	}
	50% {
		background-position: 100% 50%;
	}
	100% {
		background-position: 0% 50%;
	}
}

.MainContainer {
    display: grid;
    grid-template-columns: repeat(auto-fit, minmax(1rem, 1fr)) ;
    align-items: center;
    align-self: center;
    max-width: 1350px;
    margin-left: auto;
    margin-right: auto;
    height: 100%;
}